import { Component } from '@angular/core';
import { AppService } from "../../../services/App.service";
import { Utils } from '../../../services/Utils';
import { NativeService } from "../../../services/NativeService"
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Api, ContenType, Method } from '../../../utils/appConstant';
import { SkuLot } from '../../../models/SkuLotValResultModel';

/**
 * Generated class for the CheckintrayPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-checkintray',
  templateUrl: 'checkintray.html',
})
export class CheckintrayPage {
  public lpnDetail: any;//容器明细信息
  public asnHeader: any;//入库单头表信息
  public asnDetails: any;//入库单明细表信息
  public sku: any;//物品信息
  public skuPackageDetails: any;//包装管理集合
  public skuPackageDetailsItem: any = {};//包装管理选中
  public asnLpnId: String;//容器ID
  public lpnCode: String;//容器编码
  public lpnStatus: String;//容器物品接收状态

  public skuId: String;//物品ID
  public skuName: String;//物品名称
  public isSn: number = 0;//是否为序列号管理 1,序列号管理  0,,非序列号管理
  public serialNumber: string[] = [];//序列号已扫描集合
  public Sn: any;//序列号集合
  public serialNumberMode: string;//序列号实体
  public InNumberLength: number = 0;//已收序列号数量
  public ScanNumberLength: number = 0;//已扫描序列号数量
  public isTilebool: boolean = true;
  public NumberListFalag: number = 1; //扫描标识 1：已扫描 2:已收货

  public asnBillId: String;//订单ID
  public asnBillNo: String;//订单编号
  public asnDetailId: String;//入库单明细ID
  public billType: String;//单据类型
  public wspId: String;//包装ID
  public wspdId: String;//包装明细ID
  public whName: String;//库房名称
  public skuSpec: String;//规格
  public asnBillState: Number;//单据状态
  public lpnQty: Number;//物品数量
  public umName: String;//单位
  public sname: String; //供应商
  public ownerName: String; //货主
  public whId: String;//库房ID
  public lotArray: SkuLot = new SkuLot();
  public planQty: string;//计划数量
  public scanQty: string;//已完成数量

  public locId: String;//库位ID
  public locCode: String = "STAGE";//库位
  public isShow: Boolean = false;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public appService: AppService,
    public nativeService: NativeService) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CheckintrayPage');
  }
  /**
   * LPN输入框按下回车输入焦点移动
   */
  keyEnter(event) {
    if (event && event.keyCode == 13) {
      if (Utils.isEmpty(this.lpnCode)) {
        this.nativeService.showToast('请输入容器编码');
        return;
      }
      this.getData(this.lpnCode);
    }
  }
  getData(lpnCode) {
    let params = {
      lpnCode: lpnCode
    }
    this.appService.httpRequest(Api.Instock + Api.GetAsnLpnDetail, Method.Post, params, ContenType.Json, result => {
      if (Utils.isEmpty(result.data)) {
        this.nativeService.showToast('当前信息不存在');
      } else {
        console.log(result.data);
        if (Utils.isNotEmpty(result.data.asnLpnDetail)) {
          this.lpnDetail = result.data.asnLpnDetail;
          this.asnLpnId = this.lpnDetail.asnLpnId;
          this.lpnCode = this.lpnDetail.lpnCode;
          this.lpnStatus = this.lpnDetail.lpnStatus;
          this.asnBillId = this.lpnDetail.asnBillId;
          this.asnDetailId = this.lpnDetail.asnDetailId;
          this.wspId = this.lpnDetail.wspId;
          this.skuSpec = this.lpnDetail.skuSpec;
          this.lpnQty = this.lpnDetail.lpnQty;
          this.umName = this.lpnDetail.umName;
        }
        if (Utils.isNotEmpty(result.data.asnHeader)) {
          this.asnHeader = result.data.asnHeader;
          this.whId = this.asnHeader.whId;
          this.whName = this.asnHeader.whName;
          this.sname = this.asnHeader.sName;
          this.ownerName = this.asnHeader.ownerName;
          this.asnBillState = this.asnHeader.asnBillState;
          this.asnBillNo = this.asnHeader.asnBillNo;
          this.billType = this.asnHeader.billType;
        }
        if (Utils.isNotEmpty(result.data.asnDetails)) {
          this.planQty = result.data.asnDetail.planCountQty;
          this.scanQty = result.data.asnDetail.scanCountQty;
          this.asnDetails = result.data.asnDetails[0];
          //获取30个批属性的值
          for (let index = 1; index <= 30; index++) {
            let lotkey = 'skuLot' + index;
            let lotval = this.asnDetails[lotkey];
            this.lotArray[lotkey] = lotval;
          }
          if (Utils.isNotEmpty(result.data.skuPackageDetails)) {
            this.skuPackageDetails = result.data.skuPackageDetails;
            let pageUm = this.skuPackageDetails.filter(x => x.wsuCode == this.asnDetails.umCode);
            if (Utils.isNotEmpty(this.skuPackageDetails.filter(x => x.wsuCode == this.asnDetails.umCode))) {
              this.skuPackageDetailsItem = pageUm[0];
            }
          }
        }
        if (Utils.isNotEmpty(result.data.sku)) {
          this.sku = result.data.sku;
          this.skuId = this.sku.skuId;
          this.skuName = this.sku.skuName;
          this.isSn = this.sku.isSn;
        }
        this.isShow = true;
      }
    });
  }
  /**
  * 查看序列号
  */
  numclick() {
    this.isSn = 3;
  }
  /**
  * 收货提交
  */
  btnOk() {
    let arrStr = '';
    if (this.sku == null) {
      this.nativeService.showToast('请先获取物品信息');
      return;
    }
    if (Utils.isEmpty(this.locCode)) {
      this.nativeService.showToast('请填写库位');
      return;
    }
    if (this.isSn == 3) {
      this.isSn = 1;
    }
    if (this.isSn == 1) {
      if (Utils.isEmpty(this.serialNumber)) {
        this.nativeService.showToast('没有扫描序列号');
        return;
      }
      this.serialNumber.forEach(element => {
        arrStr += element + ',';
      });
      arrStr = arrStr.substring(0, arrStr.length - 1);
      if (this.serialNumber.length < this.lpnQty) {
        this.nativeService.showToast('该托盘物品序列号未全部扫描');
        return;
      }
    }
    let params = {
      skuId: this.sku.skuId,
      skuCode: this.sku.skuCode,
      isSn: this.sku.isSn,
      planCountQty: this.planQty,
      scanCountQty: this.scanQty,
      wspId: this.wspId,
      wspdId: this.skuPackageDetailsItem.wspdId,
      snDetailCode: arrStr,
      asnLpnId: this.asnLpnId,
      lpnCode: this.lpnCode,
      asnBillNo: this.asnBillNo,
      scanQty: this.lpnQty,
      locCode: this.locCode,
      whId: this.whId,
      asnDetailSkuLotParam: this.lotArray,
      asnBillId: this.asnBillId,
      asnDetailId: this.asnDetailId
    };
    this.appService.httpRequest(Api.Instock + Api.StockAddByLPN, Method.Post, params, ContenType.Json, result => {
      if (Utils.isNotEmpty(result.data)) {
        //this.getData(this.lpnCode);
        this.nativeService.showToast('提交成功');
        this.clearModel();
        this.isShow = false;
      }
    });
  }
  /*返回*/
  exit() {
    this.navCtrl.pop();
  }
  /**
   * 对象初始化
   */
  clearModel() {
    this.lotArray = new SkuLot();
    this.lpnDetail = null;
    this.lpnCode = "";
    this.asnBillId = "";
    this.skuSpec = "";
    this.lpnQty = 0;
    this.umName = "";

    this.asnHeader = null;
    this.whName = "";
    this.sname = "";
    this.ownerName = "";
    this.asnBillState = 0;
    this.asnBillNo = "";
    this.billType = "";

    this.sku = null;
    this.skuName = "";
    this.isSn = 0;//是否为序列号管理 1,序列号管理  0,,非序列号管理

    this.planQty = "";
    this.scanQty = "";
    this.wspId = "";
    this.skuPackageDetailsItem = null;
    this.asnLpnId = "";
    this.locCode = "STAGE";
    this.whId = "";
    this.asnDetailId = "";
  }
}



